@import url("../../../../variables/Variables.module.css");

.wordcontainer {
  display: flex;
  height: 200px;
  gap: 20px;
  background-color: var(--color-darkblue);
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
}

.wordImage {
  min-width: 300px;
  object-fit: cover;
}

.wordInfo {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
}

.wordItem {
  display: flex;
  align-items: center;
  gap: 20px;
}

.word {
  font-family: "Montserrat-Bold";
  font-size: 30px;
  color: var(--color-blue);
}

.wordTranscription,
.wordTranslate {
  font-family: "Montserrat-Regular";
  font-size: 20px;
  color: var(--color-white);
}

.wordSubinfo {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.wordText,
.wordTextTranslate {
  font-family: "Montserrat-Regular";
  font-size: 15px;
  color: var(--color-white);
}

.wordTextTranslate {
  color: var(--color-gray);
}

.btnContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
  box-sizing: border-box;
}

.playBtn {
  height: 100px;
  width: 100px;
  cursor: pointer;
  transition: 1s;
}

.playBtn:hover {
  filter: brightness(60%);
}
